import React from 'react'
import { useState } from 'react'
import axios from 'axios';

import BioRecorder from './BioRecorder';
export default function EditPage() {

    const [audio, setAudio] = useState([]);
    const [gameData,setGameData] = useState({});
    const [x, setX] = useState(false);
    const [file, setFile] = useState(null);

    function onNewPostClick() {
        if (x) {
            setX(false)
        }
        if (!x) { setX(true) }

    }

    const onChange = (e) => {
        setGameData({
          ...gameData,
          [e.target.name]: e.target.value,
        });
      };

    const onSubmit = async (e) => {
        e.preventDefault();
        let token = localStorage.getItem("token");
        token = JSON.parse(token);
        const formData = new FormData();
        formData.append('picture', file);
       await axios.post('api/user/editUserProfile', formData, {
            headers: {
              'Content-Type': 'multipart/form-data',
              "auth-token": token
            }
          }).then((response) => {
            console.log(response.data);
          }).catch((error) => {
            console.log(error);
          });

    };


    return (
        <section className="bg-black overflow-auto">
            <div className="flex flex-col items-center justify-center mx-auto h-screen w-screen lg:py-0">

                <div className="w-full shadow dark:border md:mt-0 sm:max-w-md xl:p-0  dark:border-red-700">
                    <div className="p-6 space-y-4 bg-black  md:space-y-6 sm:p-8">

                        <h1 className="text-xl font-bold leading-tight tracking-tight text-gray-900 md:text-2xl dark:text-white">
                            Edit
                        </h1>
                        <form className="space-y-4 md:space-y-6" action="#">
                            <div>
                                <label className='text-white'>
                                    Upload a photo <br />(Leave Blank if you dont want to edit)
                                </label>
                                <br />
                                <input className="profile-input" type="file" name="picture" id="picture  "onChange={(e) => setFile(e.target.files[0])} />
                            </div>

                            <div>
                                <button onClick={onNewPostClick} className='text border-2 border-white p-3 font-semibold  mt-5 text-white'> {x ? "Don't Post" : "Click to edit BIO"}</button>
                                <div className="flex w-full p-8 border-b-2 border-rose-700">
                                    <div className="flex flex-grow ml-4">
                                        {x ? <BioRecorder /> : ""}
                                    </div>
                                </div>
                            </div>

                            <button
                                onClick = {onSubmit}
                                type="submit"
                                className="w-full border text-white bg-primary-600 hover:bg-red-500 focus:ring-4 focus:outline-none focus:ring-primary-300 font-medium rounded-lg text-sm px-5 py-2.5 text-center dark:bg-primary-600 dark:hover:bg-primary-700 "
                            >
                                Edit Profile
                            </button>

                            <p className="text-sm text-white ">
                                Changed your mind?{" "}
                                <a
                                    href="/profile"
                                    className="font-medium text-white hover:underline dark:text-primary-500"
                                >
                                    Go back
                                </a>
                            </p>
                        </form>
                    </div>
                </div>
            </div>
        </section>
    )
}
