
import ReactPlayer from "react-player";
import axios from "axios";
import { useNavigate, Link } from "react-router-dom";

import React, { useEffect, useState } from 'react'

export default function Comment({ audio, user }) {
    let navigate = useNavigate();
    const [udata, setuData] = useState({});
    const [post,setPost] = useState({});

    useEffect(() => {
        let getData = async () => {
            try {
                let ls = localStorage.getItem("token");
                ls = JSON.parse(ls)

                const config = {
                    headers: {
                        "auth-token": ls
                    }
                };
                let { data } = await axios.get(`/api/user/getUserDetailsById/${user}`,config);
                console.log(data);
                setuData(data.user);
            } catch (error) {
                console.error(error.response)
            }
        }
        getData();
    }, [udata]);


    return (
        <div className="flex w-full p-8 overflow-auto border-b border-rose-700">
            <img src={udata.imageUrl}  className="flex-shrink-0 w-12 h-12 bg-gray-400 rounded-full"/>
            <div className="flex flex-col flex-grow ml-4">
                <div className="flex">
                    <span className="font-semibold">{udata.name}</span>
                    <span className="ml-1 text-rose-700">@{udata.userName}</span>
                </div>

                <p className="mt-1">
                    <ReactPlayer controls
                        url={audio}
                        width="100%"
                        height="50px" />
                </p>



            </div>
        </div>

    )
}
