import React from 'react'
import ReactPlayer from "react-player";
import { useParams } from "react-router-dom";
import axios from "axios";
import { useState, useEffect } from "react";
import { useNavigate } from "react-router-dom";

import AudioRecorder from "./CommentAudioRecorder";
import Comment from './Comment';
import InteractPost from './InteractPost';

export default function Commentstesting2() {

    const [audioData, setAudioData] = useState({});
    const [commentsData, setCommentsData] = useState([]);
    const [userData, setUserData] = useState({});
    const [like, setLike] = useState(false);

    const [x, setX] = useState(false)
    let navigate = useNavigate();
    let params = useParams();

    let libs = [];
    let ix = [];

    useEffect(() => {
        let getData = async () => {
            try {
                let ls = localStorage.getItem("token");
                ls = JSON.parse(ls)

                const config = {
                    headers: {
                        "auth-token": ls
                    }
                };
                console.log(params.audioId)

                let adata = await axios.get(`/api/audio/viewByAudioId/${params.audioId}`, config);
                console.log(adata.data);
                setAudioData(adata.data);
                console.log(audioData);

                let cdata = await axios.get(`/api/comment/getCommentByAudio/${params.audioId}`, config);
                setCommentsData(cdata.data);
                console.log(commentsData)


            } catch (error) {
                console.error(error)
            }
        }
        getData();
    }, [audioData, commentsData]);




    commentsData.forEach((ele) => {
        libs.push(
            <Comment audio={ele.commentUrl} user={ele.commentedBy} />
        )
    })

    ix.push(<InteractPost user={audioData.producedBy} audio={audioData.audioUrl} />)

    function onNewPostClick() {
        if (x) {
            setX(false)
        }
        if (!x) { setX(true) }
    }

    async function onLikeClick() {
        try {
            console.log("Liked.")
            let ls = localStorage.getItem("token");
            ls = JSON.parse(ls)
            console.log(ls)

            const config = {
                headers: {
                    "auth-token": ls
                }
            };

            if(like==false){
             await axios.post(`/api/audio/like/${audioData._id}`,config);
             setLike(true);
            }
            else if(like==true){
            await axios.post(`/api/audio/dislike/${audioData._id}`,config);
            setLike(false);
            }
            

        } catch (error) {
            console.log(error);
        }
    }
    return (
        <div>
            <div className='bg-black'>
                <div className="flex justify-center w-screen h-screen px-4 text-white">

                    <div className="flex w-full max-w-screen-lg">
                        {/* Navbar */}
                        <div className="flex flex-col  py-4 pr-3 text-rose-700">
                            <a
                                className="px-3 py-2 mt-2 text-lg font-medium rounded-sm hover:bg-gray-300"
                                href="/feed"
                            >

                                Home
                            </a>
                        </div>


                        <div className="flex flex-col flex-grow w-2/3 border-l border-r border-rose-700">

                            <div className="flex-grow h-0 overflow-auto">

                                {/* <button onClick className='text border-2 border-red-700 p-3 font-semibold ml-10 mt-5 text-white'> {x ? "Don't Post" : "New Post"}</button> */}
                                {/* <InteractPost user={} audio={} /> */}
                                {ix}
                                <div className="flex justify-between flex-shrink-0 px-8 py-4 border-b border-rose-700">
                                    <h1 className="text-xl font-semibold ">Comments</h1>
                                </div>
                                {libs}

                            </div>
                        </div>

                        <div className="flex flex-col flex-shrink-0 w-1/4 py-4 pl-4 text-white">
                            {/* <button onClick={onLikeClick} className="font-semibold justify-centerw-40 h-10 py-1 ml-5 mt-10 mb-3 text-center text-white"> {(like==true)?"Dislike":"Like"}</button> */}
                            <button onClick={onNewPostClick} className="font-semibold justify-centerw-40 h-10 py-1 ml-5 mt-10 mb-3 text-center text-white"> Add COMMENT</button>
                            <div className="flex  flex-grow ml-4">

                                {x ? <AudioRecorder audioId={audioData._id} /> : ""}

                            </div>
                        </div>

                    </div>
                </div>

            </div>

        </div>
    )
}
