
import ReactPlayer from "react-player";
import axios from "axios";
import { useNavigate, Link } from "react-router-dom";

import React, { useEffect, useState } from 'react'

export default function Post({ audio, user }) {
    let navigate = useNavigate();
    const [udata, setuData] = useState({});
    const [post,setPost] = useState({});

    useEffect(() => {
        let getData = async () => {
            try {
                let ls = localStorage.getItem("token");
                ls = JSON.parse(ls)

                const config = {
                    headers: {
                        "auth-token": ls
                    }
                };
                let { data } = await axios.get(`/api/user/getUserDetailsById/${user}`,config);
                console.log(data.user);
                setuData(data.user);
            } catch (error) {
                console.error(error.response)
            }
        }
        getData();
    }, [udata]);

    async function onClickHandler(e){
        try {
            e.preventDefault();
            console.log(audio._id);
            navigate(`/interact2/${audio._id}`);
        } catch (error) {
            console.log(error);
        }
    }




    return (
        <div className="flex w-full p-8 overflow-auto border-b border-rose-700">
            <img src={udata.imageUrl} className="flex-shrink-0 w-12 h-12 bg-gray-400 rounded-full" />
            <div className="flex flex-col flex-grow ml-4">
                <div className="flex">
                    <span className="font-semibold hover:text-red-500 hover:cursor-pointer" onClick={()=>{
                        navigate(`/userprofile/${udata._id}`)
                    }}>{udata.name}</span>
                    <span className="ml-1 text-rose-700">@{udata.userName}</span>
                    <span className="ml-auto text-sm">{audio.createdAt.slice(0,10)}</span>
                </div>

                <p className="mt-1">
                    <ReactPlayer controls
                        url={audio.audioUrl}
                        width="100%"
                        height="50px" />
                </p>

                <div className="flex mt-2">

                    <button onClick={onClickHandler} className="btn mr-3 p-3 font-semibold hover:bg-rose-700 border border-white rounded-lg">
                       interact
                    </button>

                  
                </div>

            </div>
        </div>

    )
}
