
import ReactPlayer from "react-player";
import axios from "axios";
import { useNavigate, Link } from "react-router-dom";
import { useParams } from "react-router-dom";

import React, { useEffect, useState } from 'react'

export default function InteractPost({ audio, user }) {
   
    let navigate = useNavigate();
    let params = useParams();
    const [udata, setuData] = useState({});

    useEffect(() => {
        let getData = async () => {
            try {
                let ls = localStorage.getItem("token");
                ls = JSON.parse(ls)

                const config = {
                    headers: {
                        "auth-token": ls
                    }
                };
                console.log(user,audio);
                let { data } = await axios.get(`/api/user/getUserDetailsById/${user}`,config);
                setuData(data.user);
                console.log(data.user.name);
            } catch (error) {
                console.error(error.response);
            }
        }
        getData();
    }, [udata]);

  
    return (
        <div className="flex w-full p-8 overflow-auto border-b border-rose-700">
        <div className="flex flex-col flex-grow ml-4">
        
            <div className="flex">
                <span className="text-white font-semibold">Audio.</span>
            </div>

            <p className="mt-1">
                <ReactPlayer controls
                    url={audio}
                    width="100%"
                    height="50px" />
            </p>
        </div>
    </div>

    )
}
